.project-slider-container img {
    max-height: 68vh;
    max-width: 89vw;
    width: auto;
}

/* video size must be restricted to 1.531 ratio */

.project-video {
    max-height: min(68vh, 58.125vw);
    max-width: min(104.1vh, 89vw);
}

@media screen and (max-width: 575.98px) {
    .project-slider-container img {
        max-height: 67vw;
        max-width: 89vw;
        width: auto;
    }
}